@import url('https://fonts.googleapis.com/css2?family=Outfit:wght@200;400;600&display=swap');

*{
    padding:0px;
    margin:0px;
}

body{
    font-family:'Outfit';
}

#navBar{
    font-family:'Bakbak';
    a{
       @include colorMedidas($azultoth,20px,600);
    }
    li{
        margin-left: 20px;
    }
}

.portadaIndex{
        height: 72vh;
        background-image: url('../imagenes/portada.jpg');
        background-position: center;
        background-size: cover;
        background-color:rgb(50, 50, 50);
        background-blend-mode: soft-light;
        @media  all and (max-width: $bp-md){
            height: 42vh;
            background-image: url('../imagenes/portadamobile.jpg');
        }
        h1{
            position: relative;
            @include colorMedidas(#fff,130px,600);
            @media  all and (max-width: 1400px){
                font-size: 90px ;
            }
            @media  all and (max-width: 992px){
                font-size: 70px;
            }
            @media  all and (max-width: 712px){
                font-size: 55px;
            }       
        }    
}

.contenedorFotos{
    background-color: rgb(211, 210, 210);
    h2{
        position: absolute;
        background: rgba(0, 0, 0, 0.6);
        @include colorMedidas(#fff,20px,400);          
    }
    img{
        border-radius: 1.5rem;
    }
}

.transform:hover{
    transform: scale(1.15);
}

.transition{
    transition: all 300ms ease-in-out;
}

.destacados{
    background-color: rgb(211, 210, 210);
    h3{
        @include colorMedidas(#000,40px,400);
    }
}

.portadaConstruccion{
        height: 72vh;
        background-image: url('../imagenes/profesionales.jpg');
        background-position: center;
        object-fit: cover;
        @media  all and (max-width: $bp-md){
            height: 42vh;
            background-image: url('../imagenes/profesionalesmobile.jpg');
        }
}

.construccion{
    background-color:rgb(211, 210, 210);
    .galeriaProductos{
        h3{
        @include colorMedidas(#000,25px,400);
        }
        p{
        @include colorMedidas(#000,17px,200); 
        }
    }
}

.portadaJardin{
    height: 72vh;
    background-size: cover;
    background-image: url('../imagenes/sujardin.jpg');
    background-position: center;
    object-fit: cover;
    @media  all and (max-width: $bp-md){
        height: 42vh;
        background-image: url('../imagenes/sujardinmobile.jpg');
    }
}

.jardin{
    background-color:rgb(211, 210, 210);
    .galeria{
        h3{
        @include colorMedidas(#000,25px,400);
        }
        p{
        @include colorMedidas(#000,17px,200); 
        }
    }
}

.portadaEnvios{
    height: 72vh;
    background-image: url('../imagenes/envios.jpg');
    background-position: center;
    background-size: cover;
    @media  all and (max-width: $bp-md){
        height: 42vh;
        background-image: url('../imagenes/enviosmobile.jpg');
    }
}

.envios{
    background-color:rgb(211, 210, 210); 
    .descripcionEnvios{
        line-height: 30px;
        p{
            @include colorMedidas(#000,20px,200);
        }
        span{
            @include colorMedidas(#000,20px,400);
        }
    }
}

.portadaNosotros{
    height: 72vh;
    background-image: url('../imagenes/sobrenosotros.jpg');
    background-position: center;
    background-size: cover;
    background-color:rgb(50, 50, 50);
    background-blend-mode: soft-light;
    @media  all and (max-width: $bp-md){
        height: 42vh;
        background-image: url('../imagenes/sobrenosotrosmobile.jpg');
    }
    .textoNosotrosPortada{
        line-height: 10px;
        padding: 160px;
        @media  all and (max-width: $bp-md){
            padding: 80px;
        }
    }
    h3{
        @include colorMedidas(#fff,32px,400);
        position: relative;
        @media  all and (max-width: $bp-md){
            font-size: 25px;
        }
    }
    p{
        position: relative;
        @include colorMedidas(#fff,20px,200);
        @media  all and (max-width: $bp-md){
            font-size:15px;
        }
    }     
}

.nosotros{
    background-color:rgb(211, 210, 210); 
    .nosotrosDescripcion{
        h3{
            @include colorMedidas(#000,30px,400);
        }
        h4{
            @include colorMedidas(#000,20px,400);
        }
        p{
            @include colorMedidas(#000,20px,200);
        } 
    }
}

.contacto{
    background-color:rgb(211, 210, 210);
    p{
        @include colorMedidas (#000,20px,400);
    }
    .transform2:hover{
        transform: scale(1.1);
        background-color: $azultoth;
        color:white;
     }
    input{
        width: 100%;
        height: 60px;
    }
    textarea{
        height: 140px;
    }
    .boton{
        width: 150px;
        height: 50px;
        background-color: white;
        color: black;
    }
}

.footer{
    background-color: $azultoth;
    .box1{
        width: auto;
    }
    .box3{
        color: white;
        a{
            color: white;
        }
    }
    span{
        @include colorMedidas(#fff,18px,400);
    }
    p{
        @include colorMedidas(#fff,13px,200);
        line-height: 10px;
    }
}